import { useEffect, useState } from "react"

export default function Report() {
  const [data,setData] = useState<any>()

  useEffect(()=>{
    fetch("/api/sales-today")
      .then(r=>r.json())
      .then(setData)
  },[])

  if(!data) return <h2 style={{padding:40}}>Loading dashboard...</h2>

  return (
    <div style={{padding:40}}>
      <h1>üìä Ice Spot Owner Dashboard</h1>

      <h2>Today</h2>
      <p>Total Sales: ‚Çπ{data.todayTotal}</p>
      <p>Total Bills: {data.todayBills}</p>

      <h3>Payment Split</h3>
      <p>Cash: ‚Çπ{data.cashTotal}</p>
      <p>UPI: ‚Çπ{data.upiTotal}</p>

      <hr/>

      <h2>This Week</h2>
      <p>Sales: ‚Çπ{data.weekTotal}</p>

      <h2>This Month</h2>
      <p>Sales: ‚Çπ{data.monthTotal}</p>

      <hr/>

      <h2>Top Selling Items üç¶</h2>
      {data.topItems.map((i:any)=>(
        <div key={i[0]}>
          Item ID: {i[0]} ‚Äî Sold: {i[1]}
        </div>
      ))}
    </div>
  )
}
